<template>
	<div class="detail-page">
		<van-tabs ref="tab" v-model="active" @change="onChange">
			<template v-for="(item, i) in tabs">
				<van-tab :key="i" :name="item.value" :title="item.label"></van-tab>
			</template>
		</van-tabs>
		<div class="tab-panel" :style="{height: wrapHeight + 'px'}">
			<router-view/>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'detail',
		data() {
			return {
				tabs: [{
					value: '/guide',
					label: '培训指引'
				}, {
					value: '/examlist',
					label: '考试测验'
				}, {
					value: '/questionlist',
					label: '问卷调查'
				}, {
					value: '/scorelist',
					label: '成绩查询'
				}],
				active: '',
				contentH: 0,
				timeKey: null
			}
		},
		computed: {
			wrapHeight() {
				return this.contentH
			}
		},
		watch: {
			$route(to, from) {
				this.setActive()
			}
		},
		mounted() {
			this.setActive()
			this.$nextTick(() => {
				this.contentH = document.documentElement.clientHeight - this.$refs.tab.$el.offsetHeight
			})
		},
		methods: {
			setActive() {
				let path = this.$route.path
				this.active = path
				/*let filters = this.tabs.filter(item => item.value.indexOf(path) !== -1)
				this.active = filters.length ? filters[0].value : path*/
			},
			onChange(name) {
				this.$router.replace({
					path: name,
					query: {
						id: this.$route.query.id
					}
				}).catch(err => {
					history.back()
				})
			}
		}
	}
</script>

<style lang="scss" scoped>

</style>


